@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format(woff2), url("../fonts/ptsans.woff") format(woff);
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format(woff2), url("../fonts/ptsansbold.woff") format(woff);
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  min-height: 100vh;
  background-color: #f2f2f2;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #333333;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

.main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navigation {
  position: relative;
}

.navigation__logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;
  display: block;
  width: 185px;
  height: 96px;
  transform: translateX(-50%);
}

.navigation__btn {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 10;
  width: 23px;
  height: 22px;
  background-color: transparent;
  background-image: url("../img/svg/icon-menu.svg");
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__btn {
    display: none;
  }
}

.navigation__list {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 5;
  display: none;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    display: grid;
    justify-content: space-between;
    width: 100%;
    background-color: #ffffff;
    grid-template-columns: repeat(4, auto);
  }
}

.navigation__list::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .navigation__list::after {
    display: none;
  }
}

.navigation__item {
  padding: 20px 17px 10px;
  text-align: center;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.navigation__item:first-child {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
}
@media (min-width: 768px) {
  .navigation__item:first-child {
    box-shadow: none;
  }
}
@media (min-width: 768px) {
  .navigation__item {
    width: 153px;
    box-shadow: none;
  }
}
@media (min-width: 1200px) {
  .navigation__item {
    width: 239px;
  }
}
@media (min-width: 768px) {
  .navigation__item:nth-child(4n-2) {
    margin-right: 76px;
  }
}
@media (min-width: 1200px) {
  .navigation__item:nth-child(4n-2) {
    margin-right: 120px;
  }
}
@media (min-width: 768px) {
  .navigation__item:nth-child(4n-1) {
    margin-left: 76px;
  }
}
@media (min-width: 1200px) {
  .navigation__item:nth-child(4n-1) {
    margin-left: 120px;
  }
}

.navigation__link {
  display: inline-block;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  vertical-align: baseline;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .navigation__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
}

.navigation__link--current {
  color: #766357;
}

@media (max-width: 767px) {
  .navigation--open .navigation__list {
    display: block;
  }
}

@media (max-width: 767px) {
  .navigation--open .navigation__btn {
    background-image: url("../img/svg/icon-cross.svg");
  }
}

.no-js .navigation__btn {
  display: none;
}

.no-js .navigation__list {
  position: relative;
  display: block;
}
@media (min-width: 768px) {
  .no-js .navigation__list {
    position: static;
  }
}

.intro {
  text-align: center;
  text-transform: uppercase;
}

.intro__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}

@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__item {
  position: relative;
}

@media (min-width: 768px) {
  .advantages__item--full {
    grid-column: 1/-1;
  }
}

@media (min-width: 768px) {
  .advantages__wrapper {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
}

@media (min-width: 768px) {
  .advantages__item--full + .advantages__item--full .advantages__wrapper {
    grid-template-columns: 2fr 1fr;
  }
}

.advantages__container {
  padding: 26px 35px 24px;
  text-align: center;
  text-transform: uppercase;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .advantages__container {
    min-height: 256px;
    padding: 42px 30px 22px;
  }
}
@media (min-width: 1200px) {
  .advantages__container {
    padding: 52px 40px 25px;
  }
}

.advantages__item--full:first-child .advantages__container {
  padding-top: 71px;
  padding-bottom: 38px;
}
@media (min-width: 768px) {
  .advantages__item--full:first-child .advantages__container {
    padding-top: 54px;
    padding-bottom: 22px;
  }
}
@media (min-width: 1200px) {
  .advantages__item--full:first-child .advantages__container {
    padding: 54px 56px 35px;
  }
}

.advantages__item--full:first-child .advantages__container::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 36px;
  background-image: url("../img/svg/advantages-triangle.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-width: 768px) {
  .advantages__item--full:first-child .advantages__container::before {
    display: none;
  }
}

.advantages__title {
  margin: 0 0 11px;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}
@media (min-width: 768px) {
  .advantages__title {
    padding: 0 20px;
    font-size: 21px;
  }
}
@media (min-width: 1200px) {
  .advantages__title {
    max-width: 175px;
    margin: 0 auto 12px;
    padding: 0 10px;
  }
}

.advantages__item p {
  margin: 0;
}

.advantages__description br {
  display: none;
}
@media (min-width: 768px) {
  .advantages__description br {
    display: block;
  }
}

.advantages__container p + p {
  margin-top: 17px;
}
@media (min-width: 768px) {
  .advantages__container p + p {
    margin-top: 23px;
  }
}

.advantages__wrapper .advantages__container {
  padding: 39px 20px 35px;
  color: #ffffff;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .advantages__wrapper .advantages__container {
    padding: 53px 30px 21px;
  }
}
@media (min-width: 1200px) {
  .advantages__wrapper .advantages__container {
    padding: 53px 83px 35px;
  }
}

.advantages__wrapper .advantages__title {
  color: #ffffff;
}

.advantages__img {
  width: 100%;
  height: 260px;
}
@media (min-width: 768px) {
  .advantages__img {
    height: auto;
  }
}

.advantages__img img {
  display: block;
  width: 100%;
  height: 100%;
}

.advantages__services {
  padding-bottom: 44px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .advantages__services {
    display: grid;
    padding-bottom: 0;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__services-item {
  padding: 33px 15px 0 65px;
  text-transform: uppercase;
  background-repeat: no-repeat;
  background-position: 18px 23px;
  background-size: 40px 40px;
}
@media (min-width: 768px) {
  .advantages__services-item {
    min-height: 331px;
    padding: 157px 33px 60px;
    text-align: center;
    background-position: center 61px;
  }
}
@media (min-width: 1200px) {
  .advantages__services-item {
    padding: 170px 55px 60px;
    background-position: center 70px;
  }
}

.advantages__services-item .advantages__title {
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .advantages__services-item .advantages__title {
    margin-bottom: 22px;
  }
}

.advantages__services-item--motel {
  background-image: url("../img/svg/icon-house.svg");
}
@media (min-width: 768px) {
  .advantages__services-item--motel {
    background-size: 77px 72px;
  }
}

.advantages__services-item--meal {
  background-image: url("../img/svg/icon-burger.svg");
}
@media (min-width: 768px) {
  .advantages__services-item--meal {
    background-size: 80px 67px;
  }
}
@media (min-width: 1200px) {
  .advantages__services-item--meal {
    background-size: 72px 72px;
  }
}

.advantages__services-item--souvenirs {
  background-image: url("../img/svg/icon-gift.svg");
}
@media (min-width: 768px) {
  .advantages__services-item--souvenirs {
    background-size: 85px 80px;
  }
}
@media (min-width: 1200px) {
  .advantages__services-item--souvenirs {
    background-size: 93px 77px;
  }
}

.advantages__services-item--souvenirs br {
  display: none;
}
@media (min-width: 1200px) {
  .advantages__services-item--souvenirs br {
    display: block;
  }
}

@media (min-width: 768px) {
  .advantages__item--full + .advantages__item--full .advantages__img {
    order: -1;
  }
}

.advantages__item:not(.advantages__item--full)::after {
  content: "";
  position: absolute;
  right: 20px;
  bottom: 0;
  left: 20px;
  height: 1px;
  background-color: #d6d6d6;
}
@media (min-width: 768px) {
  .advantages__item:not(.advantages__item--full)::after {
    display: none;
  }
}

.advantages__item:last-child::after {
  display: none;
}

.button {
  display: block;
  width: 284px;
  margin: 0 auto;
  padding: 16px 18px 16px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}

.search {
  padding: 42px 18px 48px;
  text-align: center;
  text-transform: uppercase;
}

.search__title {
  margin: 0 0 19px;
  font-size: 21px;
  line-height: 26px;
}

.search__text {
  margin: 0 0 32px;
}

.search__text br {
  display: none;
}

.map {
  width: 100%;
  height: 350px;
}

.map iframe {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
}

.map__img {
  display: none;
  width: 100%;
  height: 350px;
}

.map__img img {
  display: block;
  width: 100%;
  height: 100%;
}

.no-js iframe {
  display: none;
}

.gallery__item {
  padding: 24px 20px 10px;
  border-bottom: 1px solid #e5e5e5;
}

.gallery__img {
  display: block;
  height: 280px;
}

.gallery__img img {
  display: block;
  width: 100%;
  height: 100%;
}

.gallery__title {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  text-transform: uppercase;
}

.gallery__like {
  display: grid;
  align-items: baseline;
  padding: 0;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: rgba(0, 0, 0, 0.3);
  background-color: transparent;
  border: none;
  cursor: pointer;
  gap: 14px;
  grid-template-columns: auto auto;
}

.gallery__like span {
  color: #000000;
}

.video {
  padding: 42px 20px 48px;
  text-align: center;
  text-transform: uppercase;
}

.video__title {
  margin: 0 0 26px;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}

.video__text {
  margin: 0 0 38px;
  padding: 0 10px;
}

.video__image {
  position: relative;
  height: 177px;
}

.video__img img {
  display: block;
  width: 100%;
  height: 100%;
}

.video__play {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 48px;
  height: 54px;
  color: #ffffff;
  background-color: transparent;
  background-image: url("../img/svg/icon-play.svg");
  background-repeat: no-repeat;
  border: none;
  transform: translate(-50%, -50%);
  opacity: 0.7;
}

.contact__label {
  padding-left: 16px;
  text-transform: uppercase;
}

.contact__input {
  width: 100%;
  height: 58px;
  padding: 6px 12px 6px 12px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #f2f2f2;
  border: 4px solid transparent;
}

.options__label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.form__fieldset {
  margin: 0;
  padding: 24px 20px 23px;
  border: none;
  border-bottom: 1px solid #e5e5e5;
}

.form__fieldset-title {
  margin: 0 0 20px;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
}

.form__message {
  width: 100%;
  height: 150px;
  padding: 15px 12px 12px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #f2f2f2;
  border: 4px solid transparent;
  resize: none;
}

.form__note {
  display: block;
  order: 1;
  font-size: 12px;
  line-height: 21px;
  text-align: right;
  color: rgba(51, 51, 51, 0.6);
  text-transform: uppercase;
}

.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.social__item:not(:last-child) {
  margin-right: 6px;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  color: #ffffff;
  background-color: #81b3d2;
}

.social__link svg {
  fill: currentColor;
}

.social__link:active svg {
  opacity: 0.3;
}

.footer {
  padding: 42px 20px 32px;
  text-align: center;
  text-transform: uppercase;
}

.footer__appeal {
  display: inline-block;
  margin-bottom: 31px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #000000;
  text-decoration: none;
}

.footer__copyright {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #000000;
  text-decoration: none;
}

.footer__copyright-text {
  margin-right: 12px;
}

.footer__social {
  margin-bottom: 30px;
}